* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: 'Inter';
  src: url('InterDisplay-Bold.woff2') format('woff2-variations');
  font-weight: 700;
}

@keyframes animateBackground {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

body {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #000000;
  background-size: 400% 400%;
  animation: animateBackground 5s linear infinite; /* Faster color change */
}

#my-name {
  font-family: 'Inter', sans-serif;
  color: #ffffff;
  font-size: 15 rem;
}